body {
  padding: 5px;
 
}

html, body {
  overflow: hidden;
}

nav ul a {
color:#212121 !important;
}



.output {
  overflow-y: scroll !important;
  height: 90vh;


}

.input {
  overflow-y: scroll !important;
  height: 90vh;
  border-right: 1px solid grey;

}

.shadow {
  box-shadow:         inset 0 0 4px grey;
  border-radius: 4px;
}

